<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>position</td>
<td>Object</td>
<td></td>
<td><code v-pre="">required</code> Specify the location where the flash point effect entity is added. structure:{ lng: number, lat: number, height: number } or Cesium.Cartesian3</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> Specifies whether the point is visible.</td>
</tr>
<tr>
<td>pixelSize</td>
<td>Number</td>
<td><code v-pre="">10</code></td>
<td><code v-pre="">optional</code> Specify the pixel value of the flash point effect point.</td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre="">'white'</code></td>
<td><code v-pre="">optional</code> Specify the flash point effect color.</td>
</tr>
<tr>
<td>deviationAlpha</td>
<td>Number</td>
<td><code v-pre="">0.05</code></td>
<td><code v-pre="">optional</code> Specify the flash point effect color Alpha value to change the scale, the larger the value, the faster the flash. Ranges: (0, 1)</td>
</tr>
</tbody>
</table>
<hr>
<h2>Events</h2>
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject }</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          position: { lng: 117.217124, lat: 31.809777, height: 200 }
        }
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
        },
        subReady({cesiumObject}) {
          const { Cesium, viewer } = this.cesiumInstance
          viewer.scene.globe.depthTestAgainstTerrain = true
          cesiumObject.createPromise.then(() => {
            viewer.zoomTo(viewer.entities, new Cesium.HeadingPitchRange(0, Cesium.Math.toRadians(-90), 1000))
          })
        }
      }
    }
  </script>